import * as THREE from 'three';
import Stats from 'stats.js';

import { Config, initMap, MyHelpers, MyObjects, MyWall, MyWalls } from './init-map';
import { StartEventLabel } from './events/start';
import { StopEvent } from './events/stop';
import { UpdateScoreEvent } from './events/update-score';
import { Player } from './player';

let scene: THREE.Scene;
let objects: MyObjects;
let walls: MyWalls;
let helpers: MyHelpers;
let renderer: THREE.WebGLRenderer;
let stats: Stats;
let updateFn = (delta: number) => { /**Does nothing */ }
let element: HTMLElement;
let score: number;
let wasKeypress = false;
let player: Player;
let isMachine = false;

const config: Config = {
	offset: { x: 0.0, y: 0.5 },
	ball: { radius: 0.05 },
	wall: { width: 0.05, height: 1, step: 0.5, amount: 10 },
	speed: 1,
};

interface Obstacle {
	pos: { x: number };
	range: { bottom: number, top: number };
	isNext?: boolean;
}

interface State {
	vel: { x: number; y: number; };
	obstacles?: Obstacle[];
}

const INIT_STATE: State = {
	vel: { x: 0.5, y: 0.0 }
};

let state: State;

const g = 0.03;

export function initThreeJs(elem: HTMLElement) {
	element = elem;
	renderer = new THREE.WebGLRenderer();
	renderer.setSize(window.innerWidth, window.innerHeight);
	element.appendChild(renderer.domElement);

	const map = initMap(config);
	objects = map[0];
	walls = map[1];
	helpers = map[2];

	scene = new THREE.Scene();
	scene.add(...Object.values(objects));
	// scene.add(...Object.values(helpers));
	scene.add(...walls.map(({ top, bottom }) => [top, bottom]).reduce((a, b) => [...a, ...b], []));

	stats = new Stats();
	stats.showPanel(0);
	element.appendChild(stats.dom);

	window.addEventListener('resize', handleWindowResize, false);

	renderer.setAnimationLoop(animation);

	player = new Player(jump, elem);

	element.addEventListener(StartEventLabel, startGame);
}

function getWallRange(): { bottom: number; top: number; } {
	const bottom = (Math.random() * 0.2) + 0.2;
	return { bottom, top: bottom + 0.4 };
}

function startGame(e: any) {
	isMachine = e.detail;
	window.addEventListener('keydown', jump, false);
	window.addEventListener('touchstart', jump, false);

	state = {
		...INIT_STATE,
		vel: {
			...INIT_STATE.vel
		}
	}
	objects.ball.position.y = config.offset.y;
	objects.ball.position.x = config.offset.x;

	state.obstacles = [];

	for (let i = 0; i < 10; i++) {
		const pos = { x: 0.5 + (config.wall.step * i) };
		const range = getWallRange();

		state.obstacles[i] = {
			pos,
			range,
			isNext: i === 0
		};

		const { bottom, top } = walls[i];
		bottom.position.x = pos.x;
		bottom.position.y = range.bottom - (config.wall.height / 2);

		top.position.x = pos.x;
		top.position.y = range.top + (config.wall.height / 2);

		((bottom as THREE.Mesh).material as THREE.MeshNormalMaterial).wireframe = false;
		((top as THREE.Mesh).material as THREE.MeshNormalMaterial).wireframe = false;

		walls[i].isNext = true;
	}

	score = 0;

	updateFn = update;
}

function stopGame() {
	window.removeEventListener('keydown', jump, false);
	window.removeEventListener('touchstart', jump, false);

	element.dispatchEvent(new StopEvent(score));
	updateFn = (delta: number) => { /**Does nothing */ };
}

function handleWindowResize() {
	renderer.setSize(window.innerWidth, window.innerHeight);
}

function jump() {
	wasKeypress = true;
	// (document.getElementById("myAudio") as any).play();
}

function update(delta: number) {
	if (objects.ball.position.y > 0.05 && objects.ball.position.y < 0.95) {
		const nextWall = walls.find(w=>w.isNext) as MyWall;
		!isMachine && player.savePlay([objects.ball.position.y, state.vel.y, nextWall.bottom.position.y + (config.wall.height / 2), wasKeypress]);
		isMachine && player.calculateNextStep([objects.ball.position.y, state.vel.y, nextWall.bottom.position.y + (config.wall.height / 2)]);

		if (wasKeypress) {
			state.vel.y = 0.01 * config.speed;
		}
		wasKeypress = false;

		state.vel.y -= g * delta;
		objects.ball.position.y += state.vel.y;

		walls.forEach(w => {
			const prev = w.bottom.position.x;
			w.bottom.position.x -= state.vel.x * delta;
			w.top.position.x -= state.vel.x * delta;

			if (Math.abs(w.bottom.position.x) < config.ball.radius + (config.wall.width / 2)) {
				const tmp = w.bottom.position.y + (config.wall.height / 2);
				const tmp2 = w.top.position.y - (config.wall.height / 2);
				if (objects.ball.position.y - tmp < config.ball.radius) {
					// ((w.bottom as THREE.Mesh).material as THREE.MeshNormalMaterial).wireframe = true;
					stopGame();
					return;
				} else if (tmp2 - objects.ball.position.y <= config.ball.radius) {
					// ((w.top as THREE.Mesh).material as THREE.MeshNormalMaterial).wireframe = true;
					stopGame();
					return;
				}
			}
			
			if (prev >= 0 && w.bottom.position.x <= 0) {
				w.isNext = false;
				score++;
				state.vel.x += 0.0005;
				element.dispatchEvent(new UpdateScoreEvent({ score, speed: state.vel.x }));
			}
		});

		if (walls[0].bottom.position.x < -1.1) {
			const tail = walls[config.wall.amount - 1];
			const head = walls.shift() as any;
			head.bottom.position.x = tail.bottom.position.x + config.wall.step;
			head.top.position.x = tail.bottom.position.x + config.wall.step;
			((head.bottom as THREE.Mesh).material as THREE.MeshNormalMaterial).wireframe = false;
			((head.top as THREE.Mesh).material as THREE.MeshNormalMaterial).wireframe = false;
			head.isNext = true;
			const range = getWallRange();
			head.bottom.position.y = range.bottom - (config.wall.height / 2);
			head.top.position.y = range.top + (config.wall.height / 2);
			walls.push(head);
		}


		let position = helpers.top.geometry.attributes.position;
		position.setXYZ(
			0,
			objects.ball.position.x,
			objects.ball.position.y,
			objects.ball.position.z,
		);
		position.setXYZ(
			1,
			nextWall.top.position.x,
			nextWall.top.position.y - (config.wall.height / 2),
			nextWall.top.position.z,
		);
		position.needsUpdate = true;

		const position2 = helpers.bottom.geometry.attributes.position;
		position2.setXYZ(
			0,
			objects.ball.position.x,
			objects.ball.position.y,
			objects.ball.position.z,
		);
		position2.setXYZ(
			1,
			nextWall.bottom.position.x,
			nextWall.bottom.position.y + (config.wall.height / 2),
			nextWall.bottom.position.z,
		);
		position2.needsUpdate = true;

	} else {
		stopGame();
	}
}

let prevTime = 0;
function animation(time: number) {
	const t = time / 1000;

	updateFn((t - prevTime) * config.speed);

	prevTime = t;
	stats.update();
	renderer.render(scene, objects.camera as THREE.Camera);
}
