/* eslint-disable @typescript-eslint/no-unused-vars */
import { StartEventLabel } from './events/start';
import { StopEventLabel } from './events/stop';
import { UpdateExportModeEventLabel } from './events/update-export-mode';

const theta1 = [
    [3.3863, -5.453, -2.9138, 2.0849],
    [-2.9743, -1.9813, -3.1036, -0.1795]
];

const theta2 = [
    [-3.5435, 6.7233, -3.7508],
    [3.5977, -6.8018, 3.7775]
];

export class Player {
    private counter = 0;
    private logNeg: string[] = [];
    private logPos: string[] = [];
    private isExport: boolean = false;
    constructor(private jump: Function, elem: HTMLElement) {
        elem.addEventListener(StartEventLabel, () => {
            this.logPos = [];
            this.logNeg = [];
        });

        elem.addEventListener(StopEventLabel, () => {
            const ratio = Math.floor(this.logNeg.length / this.logPos.length);
            const tmp = this.logNeg.filter((_, i) => i % ratio === 0);

            if (this.isExport) {
                this.download([...this.logPos, ...tmp], 'game_dataset.csv', 'csv');
            }
        });

        elem.addEventListener(UpdateExportModeEventLabel, (e:any) => {
            this.isExport = e.detail.isExport;
        })
    }

    public calculateNextStep([first, second, third]: any[]): void {
        first = (first - 0.263511) / (0.68153 - 0.263511) + 0.1;
        second = (second - (-0.019494)) / (0.0095081 - (-0.019494));
        third = (third - 0.200431) / (0.39999 - 0.200431);

        let x = [[1], [first], [second], [third]];
        x[0] = x[0].map(v => v > 1 ? 1 : (v < 0 ? 0 : v));
        x[1] = x[1].map(v => v > 1 ? 1 : (v < 0 ? 0 : v));
        x[2] = x[2].map(v => v > 1 ? 1 : (v < 0 ? 0 : v));

        const h1 = [[1], ...sigmoid(multiply(theta1, x))];
        const h2 = sigmoid(multiply(theta2, h1));

        const pos = h2[0][0];
        const neg = h2[1][0];
        console.log(pos, neg);
        if(pos > neg) {
            this.jump();
        }
    }

    public savePlay(obj: any[]): void {
        const label = obj.length - 1;
        const isPos = obj[label];
        obj[label] = isPos ? 1 : 2;
        const record = obj.join(';') + '\n';

        if (isPos) {
            this.logPos.push(record);
        } else {
            this.logNeg.push(record);
        }
    }

    private download(data: string[], filename: string, type: string) {
        var file = new Blob(data, { type });

        var a = document.createElement("a"),
            url = URL.createObjectURL(file);
        a.href = url;
        a.download = filename;
        document.body.appendChild(a);
        a.click();
        setTimeout(function () {
            document.body.removeChild(a);
            window.URL.revokeObjectURL(url);
        }, 0);
    }
}

function multiply(a: number[][], b: number[][]): number[][] {
    var aNumRows = a.length, aNumCols = a[0].length,
        bNumRows = b.length, bNumCols = b[0].length,
        m = new Array(aNumRows);  // initialize array of rows
    for (var r = 0; r < aNumRows; ++r) {
        m[r] = new Array(bNumCols); // initialize the current row
        for (var c = 0; c < bNumCols; ++c) {
            m[r][c] = 0;             // initialize the current cell
            for (var i = 0; i < aNumCols; ++i) {
                m[r][c] += a[r][i] * b[i][c];
            }
        }
    }
    return m;
}

function sigmoid(m: number[][]): number[][] {
    for (let i = 0; i < m.length; i++) {
        for (let l = 0; l < m[0].length; l++) {
            m[i][l] = 1 / (1 + Math.pow(Math.E, -1 * m[i][l]));
        }
    }

    return  m;
}