import * as THREE from 'three';

export type Objects = string; //'camera' | /*'box' |*/ 'ball' | /*'axesHelper' |*/ 'light';

export type MyObjects = Record<Objects, THREE.Object3D>;
export type MyWall = { top: THREE.Mesh, bottom: THREE.Mesh, isNext?: boolean };
export type MyWalls = MyWall[];
export type MyHelpers = { top: THREE.Line, bottom: THREE.Line };

export interface Config {
    offset: { x: number, y: number };
    ball: { radius: number };
    wall: { width: number, height: number, step: number, amount: number };
    speed: number;
}

export function initMap(config: Config): [MyObjects, MyWalls, MyHelpers] {
    const camera = new THREE.PerspectiveCamera(45, 2 / 1, 0.1, 10);
    camera.position.z = 1.70;
    // camera.position.z = 5;
    camera.position.x = config.offset.x;
    camera.position.y = config.offset.y;

    const box = new THREE.Mesh(new THREE.BoxGeometry(2, 1, 1), new THREE.MeshNormalMaterial({ side: THREE.BackSide }));
    box.position.x = config.offset.x;
    box.position.y = config.offset.y;

    const ball = new THREE.Mesh(new THREE.SphereGeometry(config.ball.radius, 20, 20), new THREE.MeshNormalMaterial());
    ball.position.x = config.offset.x;
    ball.position.y = config.offset.y;

    const walls: MyWalls = [];

    for (let i = 0; i < 10; i++) {
        const top = new THREE.Mesh(new THREE.BoxGeometry(config.wall.width, config.wall.height, 1), new THREE.MeshNormalMaterial());
        top.position.x = 2;
        const bottom = new THREE.Mesh(new THREE.BoxGeometry(config.wall.width, config.wall.height, 1), new THREE.MeshNormalMaterial());
        bottom.position.x = 2;
        walls.push({ top, bottom });
    }

    const axesHelper = new THREE.AxesHelper(5);

    const light = new THREE.AmbientLight(0x404040);



    const positions = new Float32Array([
        0, 0, 0,
        0.4, 0.2, 0.2
    ]);
    const geometry = new THREE.BufferGeometry();
    geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));
    const positions2 = new Float32Array([
        0, 0, 0,
        0.4, 0.2, 0.2
    ]);
    const geometry2 = new THREE.BufferGeometry();
    geometry2.setAttribute('position', new THREE.BufferAttribute(positions2, 3));

    const helpers: MyHelpers = {
        top: new THREE.Line(geometry, new THREE.LineBasicMaterial({ color: 0xffff00 })),
        bottom: new THREE.Line(geometry2, new THREE.LineBasicMaterial({ color: 0xffff00 })),
        // bottom: new THREE.Line(new THREE.BufferGeometry().setFromPoints(points), new THREE.LineBasicMaterial({ color: 0xffff00 })),
    };
    helpers.top.geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));

    return [
        {
            camera,
            box,
            ball,
            // axesHelper,
            light,
        },
        walls,
        helpers
    ];
}