import React, { useEffect, useRef, useState } from 'react';
import { StartEvent } from '../common/events/start';
import { StopEventLabel } from '../common/events/stop';
import { UpdateExportModeEvent } from '../common/events/update-export-mode';
import { UpdateScoreEventLabel } from '../common/events/update-score';
import { initThreeJs } from '../common/init-three';
import './App.css';

function App(): JSX.Element {
  const selfRef = useRef<HTMLDivElement>(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [isMachine, setIsMachine] = useState(false);
  const [isExport, setExport] = useState(false);
  const [score, setScore] = useState(null);
  const [speed, setSpeed] = useState(null);

  function startGame() {
    setIsPlaying(true);
    if (selfRef.current) {
      selfRef.current?.dispatchEvent(new StartEvent(isMachine));
    }
    setScore(null);
    setSpeed(null);
  }

  function stopGame(e: any) {
    setIsPlaying(false);
    setScore(e.detail);
  }

  function updateScore(e: any) {
    setScore(e.detail.score);
    setSpeed(e.detail.speed);
  }

  useEffect(() => {
    if (selfRef.current) {
      selfRef.current.addEventListener(StopEventLabel, stopGame);
      selfRef.current.addEventListener(UpdateScoreEventLabel, updateScore);
      initThreeJs(selfRef.current);
    }
  }, []);

  useEffect(() => {
    selfRef.current?.dispatchEvent(
      new UpdateExportModeEvent(
        { isExport: isExport && !isMachine }
      )
    );
  }, [isMachine, isExport]);

  return (
    <div className="App">
      <div className={`game ${isPlaying ? '' : 'blurred'}`} ref={selfRef}></div>
      {!isPlaying
        ? <div className="menu">
          <div className="popup">
            {score !== null
              ? <>
                <h1>{score} points</h1>
                <h2>Try again!</h2>
              </> : <h2>Let's get started! </h2>}
            <button className="button purple" onClick={startGame}>Play</button>
            <br/>
            <br/>
            <br/>
            <label className="container">Machine mode
              <input type="checkbox" checked={isMachine} onChange={() => setIsMachine(!isMachine)} />
              <span className="checkmark"></span>
            </label>
            <br/>
            <label className="container">Export game data
              <input disabled={isMachine} type="checkbox" checked={isExport} onChange={() => setExport(!isExport)} />
              <span className="checkmark"></span>
            </label>
          </div>
        </div>
        : <div className="dashboard">
            <h3>{score || 0} points</h3>
            <h4>{(speed || 0).toFixed(4)} speed</h4>
          </div>}
    </div>
  );
}

export default App;
